import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const DoctorDashboard = () => {
  const [patients, setPatients] = useState([]);
  const navigate = useNavigate();

  // Log on component mount
  useEffect(() => {
    console.log("üì• DoctorDashboard mounted");
    
    const fetchPatients = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/doctors/dashboard", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });

        const data = await res.json();

        console.log("‚úÖ Fetched patients data:", data);

        setPatients(data.appointments || []);
      } catch (err) {
        console.error("‚ùå Failed to fetch patients for doctor:", err);
      }
    };

    fetchPatients();

    return () => {
      console.log("üì§ DoctorDashboard unmounted");
    };
  }, []);

  // Debug function when patient card is clicked
  const handlePatientClick = (patientId) => {
    console.log(`üñ±Ô∏è Clicked on patient ID: ${patientId}`);
    navigate(`/doctor/${patientId}`);
  };

  // Handle logout
  const handleLogout = () => {
    localStorage.removeItem("token");
    navigate("/");
  };  

  return (
    <div style={{ padding: "20px", fontFamily: "sans-serif" }}>
      <h1 style={{ fontSize: "2rem", color: "#2c3e50" }}>
        <span role="img" aria-label="Doctor">üë®‚Äç‚öïÔ∏è</span> Doctor Dashboard
      </h1>

      {/* Logout Button */}
      <button
        onClick={handleLogout}
        style={{
          backgroundColor: "#e74c3c",
          color: "white",
          border: "none",
          padding: "10px 20px",
          borderRadius: "5px",
          cursor: "pointer",
          marginBottom: "20px",
        }}
      >
        Logout
      </button>

      <h2 style={{ fontSize: "1.3rem", color: "#27ae60", marginBottom: "15px" }}>
        Assigned Patients
      </h2>

      {patients.length > 0 ? (
        <div style={{ display: "flex", flexWrap: "wrap", gap: "15px" }}>
          {patients.map((patient) => (
            <div
              key={patient.patient_id}
              style={{
                border: "1px solid #ccc",
                padding: "15px",
                width: "220px",
                borderRadius: "10px",
                background: "#f9f9f9",
                cursor: "pointer",
              }}
              onClick={() => handlePatientClick(patient.patient_id)}
            >
              <h3 style={{ margin: "0 0 10px", color: "#2980b9" }}>
                {patient.patient_name}
              </h3>
              <p><strong>Email:</strong> {patient.email}</p>
              <p><strong>Gender:</strong> {patient.gender}</p>
            </div>
          ))}
        </div>
      ) : (
        <p>No patients assigned yet.</p>
      )}
    </div>
  );
};

export default DoctorDashboard;
