import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const DataEntryDashboard = () => {
  const [patients, setPatients] = useState([]);
  const token = localStorage.getItem("token");
  const navigate = useNavigate();

  useEffect(() => {
    const fetchPatients = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/dataEntry/patients", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        const data = await res.json();
        setPatients(data?.patients || []);
      } catch (err) {
        console.error("Data Entry fetch failed:", err);
      }
    };

    if (token) {
      fetchPatients();
    }
  }, [token]); // ‚úÖ added token as a dependency

  const handlePatientClick = (patientId) => {
    navigate(`/dataentry/${patientId}`);
  };

  // Logout function
  const handleLogout = () => {
    // Remove token from localStorage
    localStorage.removeItem("token");
    // Navigate to home page
    navigate("/");
  };

  return (
    <div style={{ padding: "20px" }}>
      <h1>Data Entry Dashboard</h1>
      
      {/* Logout Button */}
      <button
        onClick={handleLogout}
        style={{
          backgroundColor: "#e74c3c",
          color: "white",
          border: "none",
          padding: "10px 20px",
          borderRadius: "5px",
          cursor: "pointer",
          marginBottom: "20px",
        }}
      >
        Logout
      </button>

      <h2>Patients</h2>
      <div style={{ display: "flex", flexWrap: "wrap", gap: "20px" }}>
        {patients.map((p) => (
          <div
            key={p.patient_id}
            style={{
              border: "1px solid #ccc",
              borderRadius: "10px",
              padding: "10px",
              cursor: "pointer",
              width: "200px",
            }}
            onClick={() => handlePatientClick(p.patient_id)}
          >
            <strong>{p.name}</strong>
            <p>Age: {p.age}</p>
            <p>Gender: {p.gender}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default DataEntryDashboard;
