import React, { useEffect, useState } from "react";
import { useNavigate } from 'react-router-dom';

const AdminDashboard = () => {
  const navigate = useNavigate();
  const [users, setUsers] = useState([]);
  const [filter, setFilter] = useState("all");

  // Fetch Users
  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/users", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
        const data = await res.json();
        setUsers(Array.isArray(data.results) ? data.results : []);
      } catch (err) {
        console.error("Admin fetch failed:", err);
      }
    };

    fetchUsers();
  }, []);

  // Delete User
  const handleDelete = async (id) => {
    const confirmDelete = window.confirm("Are you sure you want to delete this user?");
    if (!confirmDelete) return;

    try {
      const res = await fetch(`http://localhost:5000/api/users/${id}`, {
        method: "DELETE",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
          "Content-Type": "application/json"
        },
      });

      const result = await res.json();

      if (res.ok) {
        setUsers((prevUsers) => prevUsers.filter((u) => u.user_id !== id));
        alert(result.message || "User deleted successfully.");
      } else {
        const errorMessage = typeof result.message === "string"
          ? result.message
          : JSON.stringify(result.message);
        alert(`Delete failed: ${errorMessage}`);
      }
    } catch (err) {
      console.error("Delete failed:", err);
      alert("An error occurred while deleting the user.");
    }
  };

  const filteredUsers = filter === "all" ? users : users.filter((u) => u.role === filter);

  const categories = [
    { label: "Doctors", role: "doctor" },
    { label: "Frontdesk", role: "frontdesk" },
    { label: "Data Entry", role: "dataentry" },
  ];

  // Logout function
  const handleLogout = () => {
    // Remove token from localStorage
    localStorage.removeItem("token");
    // Navigate to home page
    navigate("/");
  };

  return (
    <div style={{ padding: "2rem" }}>
      <h1>Admin Dashboard</h1>

      {/* Logout Button */}
      <button
        onClick={handleLogout}
        style={{
          backgroundColor: "#e74c3c",
          color: "white",
          border: "none",
          padding: "10px 20px",
          borderRadius: "5px",
          cursor: "pointer",
          marginBottom: "20px",
        }}
      >
        Logout
      </button>

      <div style={{ display: "flex", gap: "1rem", marginBottom: "2rem" }}>
        <div
          onClick={() => setFilter("all")}
          style={{
            padding: "1rem",
            backgroundColor: "#f0f0f0",
            borderRadius: "8px",
            cursor: "pointer",
            width: "150px",
            textAlign: "center",
            boxShadow: "0 0 10px rgba(0,0,0,0.1)",
          }}  
        >
          <strong>All</strong>
          <p>{users.length} users</p>
        </div>

        {categories.map(({ label, role }) => (
          <div
            key={role}
            style={{
              padding: "1rem",
              backgroundColor: "#f0f0f0",
              borderRadius: "8px",
              width: "150px",
              textAlign: "center",
              boxShadow: "0 0 10px rgba(0,0,0,0.1)",
              position: "relative"
            }}
          >
            <div style={{ cursor: "pointer" }} onClick={() => setFilter(role)}>
              <strong>{label}</strong>
              <p>{users.filter((u) => u.role === role).length} users</p>
            </div>
            <button
              onClick={() => navigate(`/${role}/register`)}
              style={{
                marginTop: "0.5rem",
                padding: "0.4rem 0.8rem",
                backgroundColor: "#007bff",
                color: "white",
                border: "none",
                borderRadius: "4px",
                cursor: "pointer",
                fontSize: "0.8rem",
              }}
            >
              + Add
            </button>
          </div>
        ))}
      </div>

      <h3 style={{ textTransform: "capitalize" }}>{filter} List</h3>

      {filteredUsers.length > 0 ? (
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))",
            gap: "1rem",
          }}
        >
          {filteredUsers.map((user, idx) => (
            <div
              key={idx}
              style={{
                border: "1px solid #ccc",
                padding: "1rem",
                borderRadius: "8px",
                position: "relative",
              }}
            >
              <p><strong>Name:</strong> {user.name}</p>
              <p><strong>Email:</strong> {user.email}</p>
              <p><strong>Role:</strong> {user.role}</p>
              {user.phone && <p><strong>Phone:</strong> {user.phone}</p>}
              {user.specialization && <p><strong>Specialization:</strong> {user.specialization}</p>}
              {user.shift && <p><strong>Shift:</strong> {user.shift}</p>}
              {user.department && <p><strong>Department:</strong> {user.department}</p>}

              <button
                onClick={() => handleDelete(user.user_id)}
                style={{
                  marginTop: "1rem",
                  padding: "0.5rem 1rem",
                  backgroundColor: "#ff4d4d",
                  color: "white",
                  border: "none",
                  borderRadius: "4px",
                  cursor: "pointer",
                }}
              >
                Delete
              </button>
            </div>
          ))}
        </div>
      ) : (
        <p>No users found for this category.</p>
      )}
    </div>
  );
};

export default AdminDashboard;
