import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";

const DoctorPatientDetails = () => {
  const { patient_id } = useParams();
  const navigate = useNavigate();

  const [patient, setPatient] = useState(null);
  const [description, setDescription] = useState("");
  const [loading, setLoading] = useState(true);
  const [successMsg, setSuccessMsg] = useState("");
  const [errorMsg, setErrorMsg] = useState("");

  useEffect(() => {
    const fetchPatientDetails = async () => {
      try {
        const response = await fetch(`http://localhost:5000/api/doctors/dashboard/${patient_id}`, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });

        const data = await response.json();

        if (response.status === 401 || response.status === 403) {
          setErrorMsg("Access denied. Please login with correct credentials.");
        } else if (response.ok) {
          setPatient(data);
        } else {
          setErrorMsg(data.message || "Patient not found");
        }
      } catch (error) {
        setErrorMsg("Failed to fetch patient details.");
      } finally {
        setLoading(false);
      }
    };

    fetchPatientDetails();
  }, [patient_id]);

  const handlePrescriptionSubmit = async (e) => {
    e.preventDefault();
    setSuccessMsg("");
    setErrorMsg("");

    try {
      const response = await fetch("http://localhost:5000/api/doctors/dashboard/prescribe", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        body: JSON.stringify({ patient_id, description }),
      });

      const data = await response.json();

      if (response.ok) {
        setSuccessMsg(data.message);
        setDescription("");
      } else {
        setErrorMsg(data.message || "Failed to submit prescription.");
      }
    } catch (error) {
      setErrorMsg("Error submitting prescription.");
    }
  };

  if (loading) return <p>Loading patient details...</p>;
  if (errorMsg) return <p style={{ color: "red" }}>{errorMsg}</p>;

  return (
    <div style={{ padding: "20px", fontFamily: "sans-serif" }}>
      <h2 style={{ color: "#2c3e50" }}>
        <span role="img" aria-label="stethoscope">ü©∫</span> Patient Details
      </h2>

      {patient ? (
        <div style={{ marginBottom: "20px", background: "#f0f0f0", padding: "15px", borderRadius: "10px" }}>
          <p><strong>Name:</strong> {patient.name}</p>
          <p><strong>Age:</strong> {patient.age}</p>
          <p><strong>Gender:</strong> {patient.gender}</p>
          <p><strong>Phone:</strong> {patient.phone}</p>
          <p><strong>Email:</strong> {patient.email}</p>
          <p><strong>Address:</strong> {patient.address}</p>
          <p><strong>Registered At:</strong> {new Date(patient.registered_at).toLocaleString()}</p>
        </div>
      ) : (
        <p>Patient data not found.</p>
      )}

      <h3 style={{ color: "#27ae60" }}> Write Prescription</h3>
      <form onSubmit={handlePrescriptionSubmit}>
        <textarea
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          placeholder="Enter prescription details..."
          rows={5}
          cols={60}
          required
          style={{ padding: "10px", borderRadius: "5px", border: "1px solid #ccc" }}
        />
        <br /><br />
        <button
          type="submit"
          style={{
            background: "#2980b9",
            color: "white",
            border: "none",
            padding: "10px 20px",
            borderRadius: "5px",
            cursor: "pointer"
          }}
        >
          Submit Prescription
        </button>
      </form>

      {successMsg && <p style={{ color: "green", marginTop: "10px" }}>{successMsg}</p>}
      {errorMsg && <p style={{ color: "red", marginTop: "10px" }}>{errorMsg}</p>}

      <br />
      <button
        onClick={() => navigate("/doctor/dashboard")}
        style={{
          background: "#7f8c8d",
          color: "white",
          border: "none",
          padding: "8px 16px",
          borderRadius: "5px",
          cursor: "pointer"
        }}
      >
        <span role="img" aria-label="back">üîô</span> Back to Dashboard
      </button>
    </div>
  );
};

export default DoctorPatientDetails;
