import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const FrontDeskDashboard = () => {
  const [patients, setPatients] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    fetchPatients();
  }, []);

  const fetchPatients = async () => {
    try {
      const res = await fetch("http://localhost:5000/api/patients", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      if (!res.ok) throw new Error("Failed to fetch patients");

      const data = await res.json();
      setPatients(Array.isArray(data.patients) ? data.patients : []);
    } catch (err) {
      console.error("Front desk fetch failed:", err);
      setError("Unable to load patient data.");
    } finally {
      setLoading(false);
    }
  };

  const handlePatientClick = (id) => {
    navigate(`/frontdesk/${id}`);
  };

  const handleRegister = () => {
    navigate("/frontdesk/dashboard/register");
  };

  const handleDelete = async (id, e) => {
    e.stopPropagation();
    if (!window.confirm("Are you sure you want to delete this patient?")) return;

    try {
      const res = await fetch(`http://localhost:5000/api/patients/delete/${id}`, {
        method: "DELETE",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      if (!res.ok) throw new Error("Delete failed");

      setPatients((prev) => prev.filter((patient) => patient.patient_id !== id));
    } catch (err) {
      console.error("Error deleting patient:", err);
      alert("Failed to delete patient.");
    }
  };

  const handleLogout = () => {
    // Clear token from localStorage
    localStorage.removeItem("token");
    // Redirect to login page or home page
    navigate("/login");
  };

  return (
    <div style={{ padding: "2rem", maxWidth: "1200px", margin: "0 auto" }}>
      <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: "2rem" }}>
        <h1 style={{ fontSize: "2rem", color: "#1e40af", fontWeight: "bold" }}>Front Desk Dashboard</h1>
        <button
          onClick={handleRegister}
          style={{
            backgroundColor: "#16a34a",
            color: "white",
            padding: "0.5rem 1rem",
            borderRadius: "8px",
            border: "none",
            cursor: "pointer",
            fontSize: "1rem",
          }}
        >
          + Register New Patient
        </button>
        <button
          onClick={handleLogout}
          style={{
            backgroundColor: "#dc2626",
            color: "white",
            padding: "0.5rem 1rem",
            borderRadius: "8px",
            border: "none",
            cursor: "pointer",
            fontSize: "1rem",
          }}
        >
          Logout
        </button>
      </div>

      <h2 style={{ fontSize: "1.25rem", marginBottom: "1rem", color: "#374151" }}>Registered Patients</h2>

      {loading ? (
        <p style={{ color: "#6b7280" }}>Loading patients...</p>
      ) : error ? (
        <p style={{ color: "#ef4444" }}>{error}</p>
      ) : patients.length > 0 ? (
        <div style={{ display: "grid", gridTemplateColumns: "repeat(auto-fit, minmax(280px, 1fr))", gap: "1.5rem" }}>
          {patients.map((p) => (
            <div
              key={p.patient_id}
              onClick={() => handlePatientClick(p.patient_id)}
              style={{
                backgroundColor: "#f9f9f9",
                borderRadius: "8px",
                padding: "1.5rem",
                boxShadow: "0 0 10px rgba(0,0,0,0.1)",
                cursor: "pointer",
                transition: "transform 0.2s ease-in-out",
              }}
              onMouseOver={(e) => (e.currentTarget.style.transform = "scale(1.02)")}
              onMouseOut={(e) => (e.currentTarget.style.transform = "scale(1)")}
            >
              <div style={{ marginBottom: "0.5rem" }}>
                <h3 style={{ fontSize: "1.2rem", fontWeight: "bold", color: "#111827" }}>{p.name}</h3>
                <p><strong>Age:</strong> {p.age}</p>
                <p><strong>Gender:</strong> {p.gender}</p>
                <p><strong>Phone:</strong> {p.phone}</p>
                <p><strong>Email:</strong> {p.email}</p>
                <p><strong>Address:</strong> {p.address}</p>
                <p><strong>ID:</strong> {p.patient_id}</p>
              </div>
              <button
                onClick={(e) => handleDelete(p.patient_id, e)}
                style={{
                  marginTop: "0.75rem",
                  backgroundColor: "#dc2626",
                  color: "white",
                  padding: "0.4rem 0.75rem",
                  border: "none",
                  borderRadius: "4px",
                  fontSize: "0.9rem",
                  cursor: "pointer",
                }}
              >
                Delete
              </button>
            </div>
          ))}
        </div>
      ) : (
        <p style={{ color: "#6b7280" }}>No patients found.</p>
      )}
    </div>
  );
};

export default FrontDeskDashboard;
