import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

const FrontDeskPatientDetails = () => {
  const { patient_id } = useParams();
  const [patient, setPatient] = useState(null);
  const [doctorList, setDoctorList] = useState([]);
  const [selectedDoctorUserId, setSelectedDoctorUserId] = useState("");
  const [doctorId, setDoctorId] = useState("");
  const [appointmentTime, setAppointmentTime] = useState("");
  const [appointmentSuccess, setAppointmentSuccess] = useState(false);
  const [error, setError] = useState("");

  // Fetch patient details
  useEffect(() => {
    const fetchPatientDetails = async () => {
      try {
        const res = await fetch(`http://localhost:5000/api/patients/${patient_id}`, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });

        if (!res.ok) throw new Error("Failed to fetch patient");
        const data = await res.json();
        setPatient(data.patient);
      } catch (err) {
        console.error("Error fetching patient details:", err);
        setError("Failed to fetch patient details");
      }
    };

    fetchPatientDetails();
  }, [patient_id]);

  // Fetch doctor list
  useEffect(() => {
    const fetchDoctors = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/appointments/list", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });

        if (!res.ok) throw new Error("Failed to fetch doctors");
        const data = await res.json();
        setDoctorList(data);
      } catch (err) {
        console.error("Error fetching doctor list:", err);
        setError("Failed to load doctor list");
      }
    };

    fetchDoctors();
  }, []);

  // When selected user_id changes, find and set doctor_id
  useEffect(() => {
    const selectedDoctor = doctorList.find(doc => doc.user_id === parseInt(selectedDoctorUserId));
    if (selectedDoctor) setDoctorId(selectedDoctor.doctor_id);
  }, [selectedDoctorUserId, doctorList]);

  const handleAppointmentSubmit = async (e) => {
    e.preventDefault();

    if (!doctorId || !appointmentTime) {
      setError("Please provide all appointment details.");
      return;
    }

    try {
      const res = await fetch(`http://localhost:5000/api/appointments/${patient_id}/book`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        body: JSON.stringify({
          doctor_id: doctorId,
          appointment_time: appointmentTime,
        }),
      });

      if (!res.ok) throw new Error("Failed to book appointment");

      setAppointmentSuccess(true);
      setError("");
    } catch (err) {
      console.error("Appointment error:", err);
      setError("Error booking appointment: " + err.message);
    }
  };

  if (!patient) return <p>Loading patient details...</p>;

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h2 className="text-3xl font-bold text-blue-700 mb-4">Patient Details</h2>

      <div className="mb-6 space-y-1">
        <p><strong>Name:</strong> {patient.name}</p>
        <p><strong>Age:</strong> {patient.age}</p>
        <p><strong>Gender:</strong> {patient.gender}</p>
        <p><strong>Email:</strong> {patient.email}</p>
        <p><strong>Phone:</strong> {patient.phone}</p>
        <p><strong>Address:</strong> {patient.address}</p>
      </div>

      <h3 className="text-xl font-semibold">Book an Appointment</h3>

      {appointmentSuccess && <p className="text-green-500 mb-4">Appointment booked successfully!</p>}
      {error && <p className="text-red-500 mb-4">{error}</p>}

      <form onSubmit={handleAppointmentSubmit} className="space-y-4">
        <div className="flex flex-col">
          <label htmlFor="doctorSelect" className="font-medium">Select Doctor</label>
          <select
            id="doctorSelect"
            className="p-2 border rounded-md"
            value={selectedDoctorUserId}
            onChange={(e) => setSelectedDoctorUserId(e.target.value)}
          >
            <option value="">-- Select Doctor --</option>
            {doctorList.map((doctor) => (
              <option key={`${doctor.user_id}-${doctor.doctor_id}`} value={doctor.user_id}>
                {doctor.name}
              </option>
            ))}
          </select>
        </div>

        <div className="flex flex-col">
          <label htmlFor="appointment_time" className="font-medium">Appointment Time</label>
          <input
            type="datetime-local"
            id="appointment_time"
            value={appointmentTime}
            onChange={(e) => setAppointmentTime(e.target.value)}
            className="p-2 border rounded-md"
          />
        </div>

        <button type="submit" className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700">
          Book Appointment
        </button>
      </form>
    </div>
  );
};

export default FrontDeskPatientDetails;
