import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";

const DataEntryPatientDetails = () => {
  
  const { patient_id } = useParams();
  const navigate = useNavigate();
  const token = localStorage.getItem("token");

  const [patient, setPatient] = useState({});
  const [testData, setTestData] = useState({ patient_id, test_name: "", result: "" });
  const [treatmentData, setTreatmentData] = useState({ patient_id, doctor_id: "", description: "" });
  const [testHistory, setTestHistory] = useState([]);
  const [treatmentHistory, setTreatmentHistory] = useState([]);

  useEffect(() => {
    const fetchPatient = async () => {
      const res = await fetch(`http://localhost:5000/api/dataEntry/patients/${patient_id}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      const data = await res.json();
      setPatient(data.patient);
    };
    fetchPatient();
  }, [patient_id, token]);

  useEffect(() => {
    const fetchDoctorId = async () => {
      try {
        const res = await fetch(`http://localhost:5000/api/dataEntry/patients/${patient_id}/doctor_id`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        const data = await res.json();
        if (res.ok) {
            const actualDoctorId = data.doctor_id?.doctor_id;
            setTreatmentData((prev) => ({ ...prev, doctor_id: actualDoctorId }));   
        } else {
          alert(data.message || "Doctor ID fetch failed.");
        }
      } catch (error) {
        console.error("Error fetching doctor ID:", error);
      }
    };
    fetchDoctorId();
  }, [patient_id, token]);


  const handleAddTest = async () => {
    if (!testData.test_name) return alert("Test name required");

    const res = await fetch(`http://localhost:5000/api/dataEntry/patients/${patient_id}/tests`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(testData),

    });

    const data = await res.json();
    alert(data.message);
    setTestData({ test_name: "", result: "" });
  };


  const handleAddTreatment = async () => {
    if (!treatmentData.description.trim()) return alert("Description is required");

    const res = await fetch(`http://localhost:5000/api/dataEntry/patients/${patient_id}/treatments`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(treatmentData),
    });

    console.log(treatmentData)
    const data = await res.json();
    alert(data.message);
    setTreatmentData({ description: "" });
  };


  const fetchTestHistory = async () => {
    const res = await fetch(`http://localhost:5000/api/dataEntry/patients/${patient_id}/tests`, {
      headers: { Authorization: `Bearer ${token}` },
    });
    const data = await res.json();
    setTestHistory(data.tests || []);
  };

  const fetchTreatmentHistory = async () => {
    const res = await fetch(`http://localhost:5000/api/dataEntry/patients/${patient_id}/treatments`, {
      headers: { Authorization: `Bearer ${token}` },
    });
    const data = await res.json();
    setTreatmentHistory(data.treatments || []);
  };

  return (
    <div style={{ padding: "20px" }}>
      <button onClick={() => navigate("/dataentry/dashboard")}>
        <span role="img" aria-label="Back">üîô</span> Back to Dashboard
      </button>

      <h2>Patient: {patient.name}</h2>

      <div style={{ margin: "20px 0", border: "1px solid #eee", padding: "10px" }}>
        <h3>Add Test</h3>
        <input
          type="text"
          placeholder="Test Name"
          value={testData.test_name}
          onChange={(e) => setTestData({ ...testData, test_name: e.target.value })}
        />
        <input
          type="text"
          placeholder="Result"
          value={testData.result}
          onChange={(e) => setTestData({ ...testData, result: e.target.value })}
        />
        <button onClick={handleAddTest}>
          <span role="img" aria-label="Add Test">‚ûï</span> Add Test
        </button>
      </div>

      <div style={{ margin: "20px 0", border: "1px solid #eee", padding: "10px" }}>
        <h3>Add Treatment</h3>
        <input
          type="text"
          placeholder="Description"
          value={treatmentData.description}
          onChange={(e) => setTreatmentData({ ...treatmentData, description: e.target.value })}
        />
        <button onClick={handleAddTreatment}>
          <span role="img" aria-label="Add Treatment">‚ûï</span> Add Treatment
        </button>
      </div>

      <div>
        <h3>History</h3>
        <button onClick={fetchTestHistory}>
          <span role="img" aria-label="Test History">üìú</span> Test History
        </button>
        <button onClick={fetchTreatmentHistory}>
          <span role="img" aria-label="Treatment History">üìú</span> Treatment History
        </button>
      </div>

      {testHistory.length > 0 && (
        <div style={{ marginTop: "15px" }}>
          <h4>Test History</h4>
          <ul>
            {testHistory.map((test, idx) => (
              <li key={idx}>
                <strong>{test.test_name}</strong> - {test.result || "No Result"} on {new Date(test.test_date).toLocaleDateString()}
              </li>
            ))}
          </ul>
        </div>
      )}

      {treatmentHistory.length > 0 && (
        <div style={{ marginTop: "15px" }}>
          <h4>Treatment History</h4>
          <ul>
            {treatmentHistory.map((treat, idx) => (
              <li key={idx}>
                <strong>{treat.description}</strong> by Dr. {treat.doctor_name} on {new Date(treat.treatment_date).toLocaleDateString()}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default DataEntryPatientDetails;
